.parent-container {
    background-color: #201d1b; /* Black */
}

.container1 {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px; /* Add margin bottom for spacing */
}

.output {
    border: 2px solid lightcoral; /* lightcoral */
    padding: 20px;
    width: 70%;
    background-color: #000000; /* Goldish Yellow */
    
}

.welcome,
.user-info,
.user-type {
    padding: 10px;
    margin-bottom: 10px;
}

.user-type {
    font-weight: bold;
}

.highlight-on-hover {
    box-shadow: 0 4px 8px rgba(4, 4, 4, 0.985); /* Add box-shadow for the 3D effect */
    transition: box-shadow 0.1s ease; /* Add transition effect */
    background-color: lightsalmon; /* lightcoral */
    color: white; /* Change default text color */
    border: none; /* Remove default button border */
    cursor: pointer; /* Add pointer cursor on hover */
    border-radius: 5px;
    padding: 12px 24px; /* Adjust padding */
    font-size: 16px; /* Adjust font size */
}

.highlight-on-hover:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.985); /* Increase shadow on hover for more depth */
}

.highlight-on-hover:active {
    background-color: #c3a355; /* Goldish Yellow */
}

.student-details {
    margin-top: 20px;
    border: 2px solid black;
    padding: 20px;
    width: 70%;
    display: flex;
    flex-direction: row; /* Display cards side by side */
    flex-wrap: wrap; /* Allow cards to wrap to the next line if necessary */
    justify-content: center;
    background-color: #000000; /* Goldish Yellow */
    border: 2px solid lightsalmon; /* Black */

    color: white;
}

.student-card {
    border: 1px solid #ccc;
    padding: 10px; /* lightcoraluced padding for a smaller size */
    margin: 10px; /* Add margin between cards */
    background-color: lightsalmon; /* lightcoral */
    width: calc(50% - 20px); /* Set width of each card, subtracting margin */
    max-width: 300px; /* Limit max width of the cards */
    background-color: lightsalmon; /* Goldish Yellow */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.5s ease-in-out; /* Smooth transition for box-shadow */
}

.student-card:hover {
    transform: translateY(-15px) translateX(-15px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3); /* Highlighted box-shadow on hover */
}

.student-card h3 {
    margin-top: 5;
    font-size: 16px; /* lightcoraluced font size of student name */
}

.student-card p {
    margin: 5px 0; /* lightcoraluced paragraph margin */
}

.card-title {
    font-size: 18px;
    margin-bottom: 10px;
}

.cont2 {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}
